"use client"; // Убедитесь, что это клиентский компонент

import { useEffect, useState } from 'react';

export default function Home() {
  const [validationMessage, setValidationMessage] = useState('Проверка...');

  useEffect(() => {
    // Проверяем, загружен ли Telegram.WebApp
    if (!window.Telegram || !window.Telegram.WebApp) {
      setValidationMessage('Telegram Web App не загружен.');
      return;
    }

    const tgApp = window.Telegram.WebApp;

    // Проверяем, готов ли Telegram.WebApp
    if (!tgApp.ready) {
      setValidationMessage('Telegram Web App не готов.');
      return;
    }

    // Если initData отсутствует
    if (!tgApp.initData || tgApp.initData.length === 0) {
      setValidationMessage('Ошибка: нет данных от Telegram!');
      return;
    }

    // Отправляем данные на сервер для валидации
    const validateUser = async () => {
      try {
        const response = await fetch('https://cardstradebackend20250302140309-g0abauhjbbheehh0.israelcentral-01.azurewebsites.net/api/v1/telegram/validate', {
          method: 'POST',
          headers: { 'Content-Type': 'application/json' },
          body: JSON.stringify({ InitData: tgApp.initData }),
        });

        if (response.ok) {
          setValidationMessage('Пользователь успешно проверен!');
        } else {
          setValidationMessage('Ошибка при проверке пользователя.');
        }
      } catch (error) {
        console.error('Ошибка при отправке данных на сервер:', error);
        setValidationMessage('Ошибка при проверке пользователя.');
      }
    };

    validateUser();
  }, []);

  return (
    <div className="flex justify-center items-center h-screen">
      <div id="validation" className="text-lg font-semibold">
        {validationMessage}
      </div>
    </div>
  );
}